<template>
  <div class="catalogo-container">
    <h2>Catálogo Eletrônico de Bens Padronizados</h2>
    
    <div class="search-filters">
      <div class="search-box">
        <input 
          type="text" 
          v-model="searchTerm" 
          placeholder="Buscar produto..." 
          @input="filtrarProdutos"
        >
      </div>
      
      <div class="filters">
        <select v-model="filtroCategoria" @change="filtrarProdutos">
          <option value="">Todas as categorias</option>
          <option 
            v-for="categoria in categorias" 
            :key="categoria.id" 
            :value="categoria.id"
          >
            {{ categoria.nome }}
          </option>
        </select>
      </div>
    </div>
    
    <div class="produtos-grid" v-if="produtosFiltrados.length > 0">
      <div 
        class="produto-card" 
        v-for="produto in produtosFiltrados" 
        :key="produto.id"
        @click="verDetalhesProduto(produto.id)"
      >
        <div class="produto-info">
          <h3>{{ produto.nome }}</h3>
          <p><strong>Marca:</strong> {{ produto.marca }}</p>
          <p><strong>Modelo:</strong> {{ produto.modelo }}</p>
          <p><strong>Fabricante:</strong> {{ produto.fabricante }}</p>
        </div>
        <div class="produto-status" :class="getStatusClass(produto.status)">
          {{ produto.status }}
        </div>
      </div>
    </div>
    
    <div class="empty-state" v-else>
      <p>Nenhum produto encontrado.</p>
    </div>
    
    <div class="produto-modal" v-if="produtoSelecionado" @click.self="fecharModal">
      <div class="modal-content">
        <div class="modal-header">
          <h3>{{ produtoSelecionado.nome }}</h3>
          <button class="btn-close" @click="fecharModal">×</button>
        </div>
        <div class="modal-body">
          <div class="produto-detalhes">
            <p><strong>Marca:</strong> {{ produtoSelecionado.marca }}</p>
            <p><strong>Modelo:</strong> {{ produtoSelecionado.modelo }}</p>
            <p><strong>Fabricante:</strong> {{ produtoSelecionado.fabricante }}</p>
            <p><strong>CNPJ:</strong> {{ produtoSelecionado.cnpj }}</p>
            <p v-if="produtoSelecionado.descricao"><strong>Descrição:</strong> {{ produtoSelecionado.descricao }}</p>
            <p><strong>Status:</strong> <span :class="getStatusClass(produtoSelecionado.status)">{{ produtoSelecionado.status }}</span></p>
            
            <div v-if="documentos.length > 0" class="documentos">
              <h4>Documentos</h4>
              <ul>
                <li v-for="doc in documentos" :key="doc.id">
                  <a :href="doc.arquivo_url" target="_blank">{{ doc.nome }}</a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { supabase } from '@/services/supabase'

export default {
  name: 'CatalogoEletronico',
  data() {
    return {
      produtos: [],
      produtosFiltrados: [],
      categorias: [],
      searchTerm: '',
      filtroCategoria: '',
      produtoSelecionado: null,
      documentos: []
    }
  },
  created() {
    this.carregarDados()
  },
  methods: {
    async carregarDados() {
      try {
        // Carregar produtos
        const { data: produtos, error: produtosError } = await supabase
          .from('produtos')
          .select('*')
          .eq('status', 'aprovado')
          .order('nome')
        
        if (produtosError) throw produtosError
        
        this.produtos = produtos || []
        this.produtosFiltrados = [...this.produtos]
        
        // Carregar categorias
        const { data: categorias, error: categoriasError } = await supabase
          .from('categorias')
          .select('*')
          .order('nome')
        
        if (categoriasError) throw categoriasError
        
        this.categorias = categorias || []
      } catch (error) {
        console.error('Erro ao carregar dados:', error)
        alert('Erro ao carregar dados. Por favor, tente novamente.')
      }
    },
    filtrarProdutos() {
      this.produtosFiltrados = this.produtos.filter(produto => {
        // Filtro por termo de busca
        const termMatch = this.searchTerm.trim() === '' || 
          produto.nome.toLowerCase().includes(this.searchTerm.toLowerCase()) ||
          produto.marca.toLowerCase().includes(this.searchTerm.toLowerCase()) ||
          produto.modelo.toLowerCase().includes(this.searchTerm.toLowerCase())
        
        // Filtro por categoria
        const categoriaMatch = this.filtroCategoria === '' || 
          produto.categoria_id === this.filtroCategoria
        
        return termMatch && categoriaMatch
      })
    },
    async verDetalhesProduto(id) {
      try {
        // Buscar detalhes do produto
        const { data, error } = await supabase
          .from('produtos')
          .select('*')
          .eq('id', id)
          .single()
        
        if (error) throw error
        
        this.produtoSelecionado = data
        
        // Buscar documentos do produto
        const { data: docs, error: docsError } = await supabase
          .from('documentos')
          .select('*')
          .eq('produto_id', id)
        
        if (docsError) throw docsError
        
        this.documentos = docs || []
      } catch (error) {
        console.error('Erro ao carregar detalhes do produto:', error)
      }
    },
    fecharModal() {
      this.produtoSelecionado = null
      this.documentos = []
    },
    getStatusClass(status) {
      switch (status) {
        case 'aprovado': return 'status-aprovado'
        case 'pendente': return 'status-pendente'
        case 'reprovado': return 'status-reprovado'
        default: return ''
      }
    }
  }
}
</script>

<style scoped>
.catalogo-container {
  padding: 20px;
}

h2 {
  margin-bottom: 20px;
}

.search-filters {
  display: flex;
  margin-bottom: 20px;
  gap: 15px;
}

.search-box {
  flex: 2;
}

.filters {
  flex: 1;
}

input, select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.produtos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.produto-card {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  padding: 15px;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  position: relative;
}

.produto-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.produto-info h3 {
  margin-top: 0;
  margin-bottom: 10px;
}

.produto-status {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 12px;
  text-transform: uppercase;
}

.status-aprovado {
  background-color: #2ecc71;
  color: white;
}

.status-pendente {
  background-color: #f39c12;
  color: white;
}

.status-reprovado {
  background-color: #e74c3c;
  color: white;
}

.empty-state {
  text-align: center;
  padding: 40px;
  color: #7f8c8d;
}

.produto-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 8px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
}

.modal-header {
  padding: 15px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
}

.btn-close {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.modal-body {
  padding: 15px;
}

.documentos {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #eee;
}

.documentos h4 {
  margin-top: 0;
}

.documentos ul {
  padding-left: 20px;
}

.documentos a {
  color: #3498db;
  text-decoration: none;
}

.documentos a:hover {
  text-decoration: underline;
}
</style> 