<template>
  <div class="cadastro-produto">
    <h2>Cadastro de Produto</h2>
    
    <form @submit.prevent="salvarProduto">
      <div class="form-row">
        <div class="form-group">
          <label for="nome">Nome do Produto*</label>
          <input 
            id="nome" 
            v-model="produto.nome" 
            type="text" 
            required
          >
        </div>
        
        <div class="form-group">
          <label for="categoria">Categoria*</label>
          <select 
            id="categoria" 
            v-model="produto.categoria_id" 
            required
          >
            <option value="">Selecione...</option>
            <option 
              v-for="categoria in categorias" 
              :key="categoria.id" 
              :value="categoria.id"
            >
              {{ categoria.nome }}
            </option>
          </select>
        </div>
      </div>
      
      <div class="form-row">
        <div class="form-group">
          <label for="marca">Marca*</label>
          <input 
            id="marca" 
            v-model="produto.marca" 
            type="text" 
            required
          >
        </div>
        
        <div class="form-group">
          <label for="modelo">Modelo*</label>
          <input 
            id="modelo" 
            v-model="produto.modelo" 
            type="text" 
            required
          >
        </div>
      </div>
      
      <div class="form-row">
        <div class="form-group">
          <label for="fabricante">Fabricante*</label>
          <input 
            id="fabricante" 
            v-model="produto.fabricante" 
            type="text" 
            required
          >
        </div>
        
        <div class="form-group">
          <label for="cnpj">CNPJ*</label>
          <input 
            id="cnpj" 
            v-model="produto.cnpj" 
            type="text" 
            required
          >
        </div>
      </div>
      
      <div class="form-group">
        <label for="descricao">Descrição</label>
        <textarea 
          id="descricao" 
          v-model="produto.descricao" 
          rows="4"
        ></textarea>
      </div>
      
      <div class="form-group">
        <label>Documentos</label>
        <div class="file-upload">
          <input 
            type="file" 
            @change="onFileSelect" 
            multiple
          >
        </div>
        <div class="selected-files" v-if="selectedFiles.length > 0">
          <p>Arquivos selecionados:</p>
          <ul>
            <li v-for="(file, index) in selectedFiles" :key="index">
              {{ file.name }}
            </li>
          </ul>
        </div>
      </div>
      
      <div class="form-actions">
        <button type="submit" class="btn-primary" :disabled="loading">
          {{ loading ? 'Salvando...' : 'Salvar Produto' }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import { supabase } from '@/services/supabase'

export default {
  name: 'CadastroProduto',
  data() {
    return {
      produto: {
        nome: '',
        categoria_id: '',
        marca: '',
        modelo: '',
        fabricante: '',
        cnpj: '',
        descricao: '',
        status: 'pendente'
      },
      categorias: [],
      selectedFiles: [],
      loading: false
    }
  },
  created() {
    this.carregarCategorias()
  },
  methods: {
    async carregarCategorias() {
      try {
        const { data, error } = await supabase
          .from('categorias')
          .select('*')
          .order('nome')
        
        if (error) throw error
        this.categorias = data || []
      } catch (error) {
        console.error('Erro ao carregar categorias:', error)
        alert('Erro ao carregar categorias. Por favor, tente novamente.')
      }
    },
    onFileSelect(event) {
      this.selectedFiles = Array.from(event.target.files)
    },
    async salvarProduto() {
      try {
        this.loading = true
        
        // 1. Salvar o produto
        const { data, error } = await supabase
          .from('produtos')
          .insert([{
            ...this.produto,
            tenant_id: '123e4567-e89b-12d3-a456-426614174000' // ID do tenant (temporário)
          }])
          .select()
        
        if (error) throw error
        
        const produtoId = data[0].id
        
        // 2. Upload de arquivos
        if (this.selectedFiles.length > 0) {
          const uploadPromises = this.selectedFiles.map(file => this.uploadFile(file, produtoId))
          await Promise.all(uploadPromises)
        }
        
        alert('Produto cadastrado com sucesso!')
        this.limparFormulario()
        
      } catch (error) {
        console.error('Erro ao salvar produto:', error)
        alert('Erro ao salvar produto. Por favor, tente novamente.')
      } finally {
        this.loading = false
      }
    },
    async uploadFile(file, produtoId) {
      const fileName = `${new Date().getTime()}_${file.name}`
      
      const { error: uploadError } = await supabase
        .storage
        .from('documentos')
        .upload(fileName, file)
      
      if (uploadError) throw uploadError
      
      // Obter URL do arquivo
      const { data } = supabase
        .storage
        .from('documentos')
        .getPublicUrl(fileName)
      
      // Salvar referência no banco
      const { error: docError } = await supabase
        .from('documentos')
        .insert({
          produto_id: produtoId,
          tenant_id: '123e4567-e89b-12d3-a456-426614174000', // ID do tenant (temporário)
          nome: file.name,
          tipo: file.type,
          arquivo_url: data.publicUrl
        })
      
      if (docError) throw docError
      
      return data.publicUrl
    },
    limparFormulario() {
      this.produto = {
        nome: '',
        categoria_id: '',
        marca: '',
        modelo: '',
        fabricante: '',
        cnpj: '',
        descricao: '',
        status: 'pendente'
      }
      this.selectedFiles = []
    }
  }
}
</script>

<style scoped>
.cadastro-produto {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  margin-bottom: 20px;
}

.form-row {
  display: flex;
  gap: 20px;
  margin-bottom: 15px;
}

.form-group {
  margin-bottom: 15px;
  flex: 1;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

input, select, textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

textarea {
  resize: vertical;
}

.file-upload {
  margin-bottom: 10px;
}

.selected-files {
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 15px;
}

.form-actions {
  margin-top: 20px;
}

.btn-primary {
  padding: 10px 20px;
  background-color: #2c3e50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.btn-primary:disabled {
  background-color: #95a5a6;
}
</style> 