<template>
  <div class="dashboard">
    <h2>Dashboard CPM</h2>
    
    <div class="stats-container">
      <div class="stat-card">
        <h3>Produtos Pendentes</h3>
        <div class="stat-value">{{ pendentes }}</div>
      </div>
      
      <div class="stat-card">
        <h3>Produtos Aprovados</h3>
        <div class="stat-value">{{ aprovados }}</div>
      </div>
      
      <div class="stat-card">
        <h3>Produtos Reprovados</h3>
        <div class="stat-value">{{ reprovados }}</div>
      </div>
    </div>
    
    <div class="recent-products">
      <h3>Produtos Recentes</h3>
      <table v-if="produtos.length > 0">
        <thead>
          <tr>
            <th>Nome</th>
            <th>Marca</th>
            <th>Status</th>
            <th>Data</th>
            <th>Ações</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="produto in produtos" :key="produto.id">
            <td>{{ produto.nome }}</td>
            <td>{{ produto.marca }}</td>
            <td>{{ produto.status }}</td>
            <td>{{ formatDate(produto.criado_em) }}</td>
            <td>
              <button @click="verProduto(produto.id)" class="btn-small">Ver</button>
            </td>
          </tr>
        </tbody>
      </table>
      <p v-else>Nenhum produto cadastrado.</p>
    </div>
  </div>
</template>

<script>
import { supabase } from '@/services/supabase'

export default {
  name: 'DashboardCPM',
  data() {
    return {
      produtos: [],
      pendentes: 0,
      aprovados: 0,
      reprovados: 0
    }
  },
  created() {
    this.carregarDados()
  },
  methods: {
    async carregarDados() {
      try {
        // Carregar produtos
        const { data, error } = await supabase
          .from('produtos')
          .select('*')
          .order('criado_em', { ascending: false })
          .limit(10)
        
        if (error) throw error
        
        this.produtos = data || []
        
        // Contar por status
        const statsCounts = await Promise.all([
          this.contarPorStatus('pendente'),
          this.contarPorStatus('aprovado'),
          this.contarPorStatus('reprovado')
        ])
        
        this.pendentes = statsCounts[0]
        this.aprovados = statsCounts[1]
        this.reprovados = statsCounts[2]
        
      } catch (error) {
        console.error('Erro ao carregar dados:', error)
      }
    },
    async contarPorStatus(status) {
      const { count, error } = await supabase
        .from('produtos')
        .select('id', { count: 'exact' })
        .eq('status', status)
      
      if (error) {
        console.error(`Erro ao contar produtos ${status}:`, error)
        return 0
      }
      
      return count || 0
    },
    formatDate(dateString) {
      if (!dateString) return ''
      const date = new Date(dateString)
      return date.toLocaleDateString('pt-BR')
    },
    verProduto(id) {
      this.$router.push(`/analise/${id}`)
    }
  }
}
</script>

<style scoped>
.dashboard {
  padding: 20px;
}

.stats-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.stat-card {
  background: white;
  border-radius: 8px;
  padding: 20px;
  flex: 1;
  margin: 0 10px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.stat-card:first-child {
  margin-left: 0;
}

.stat-card:last-child {
  margin-right: 0;
}

.stat-value {
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  margin-top: 10px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
}

th, td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f5f5f5;
}

.btn-small {
  padding: 5px 10px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
</style> 