<template>
  <div class="rdm-container">
    <h2>Relatório de Desempenho de Material (RDM)</h2>
    
    <form @submit.prevent="enviarFeedback">
      <div class="form-group">
        <label for="produto">Produto*</label>
        <select 
          id="produto" 
          v-model="feedback.produto_id" 
          required
        >
          <option value="">Selecione o produto...</option>
          <option 
            v-for="produto in produtos" 
            :key="produto.id" 
            :value="produto.id"
          >
            {{ produto.nome }} - {{ produto.marca }} - {{ produto.modelo }}
          </option>
        </select>
      </div>
      
      <div class="form-group">
        <label>Avaliação*</label>
        <div class="avaliacao">
          <div 
            v-for="n in 5" 
            :key="n" 
            class="estrela" 
            :class="{ ativa: feedback.avaliacao >= n }"
            @click="feedback.avaliacao = n"
          >
            ★
          </div>
        </div>
      </div>
      
      <div class="form-group">
        <label for="comentario">Descreva o problema ou feedback*</label>
        <textarea 
          id="comentario" 
          v-model="feedback.comentario" 
          rows="4" 
          placeholder="Ex: Equipamento apresentou falha após 3 meses de uso..."
          required
        ></textarea>
      </div>
      
      <div class="form-actions">
        <button type="submit" class="btn-primary" :disabled="loading">
          {{ loading ? 'Enviando...' : 'Enviar Feedback' }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import { supabase } from '@/services/supabase'

export default {
  name: 'FormularioRDM',
  data() {
    return {
      feedback: {
        produto_id: '',
        avaliacao: 0,
        comentario: ''
      },
      produtos: [],
      loading: false
    }
  },
  created() {
    this.carregarProdutos()
  },
  methods: {
    async carregarProdutos() {
      try {
        const { data, error } = await supabase
          .from('produtos')
          .select('*')
          .eq('status', 'aprovado')
          .order('nome')
        
        if (error) throw error
        this.produtos = data || []
      } catch (error) {
        console.error('Erro ao carregar produtos:', error)
        alert('Erro ao carregar produtos. Por favor, tente novamente.')
      }
    },
    async enviarFeedback() {
      if (this.feedback.avaliacao === 0) {
        alert('Por favor, selecione uma avaliação de 1 a 5 estrelas.')
        return
      }
      
      try {
        this.loading = true
        
        const { error } = await supabase
          .from('rdm_feedbacks')
          .insert({
            produto_id: this.feedback.produto_id,
            usuario_id: 'temp-user-id', // Substituir pelo ID do usuário real
            tenant_id: '123e4567-e89b-12d3-a456-426614174000', // ID do tenant (temporário)
            avaliacao: this.feedback.avaliacao,
            comentario: this.feedback.comentario
          })
        
        if (error) throw error
        
        alert('Feedback enviado com sucesso!')
        this.limparFormulario()
      } catch (error) {
        console.error('Erro ao enviar feedback:', error)
        alert('Erro ao enviar feedback. Por favor, tente novamente.')
      } finally {
        this.loading = false
      }
    },
    limparFormulario() {
      this.feedback = {
        produto_id: '',
        avaliacao: 0,
        comentario: ''
      }
    }
  }
}
</script>

<style scoped>
.rdm-container {
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
}

h2 {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

select, textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

textarea {
  resize: vertical;
}

.avaliacao {
  display: flex;
  font-size: 30px;
  cursor: pointer;
}

.estrela {
  color: #ddd;
  margin-right: 5px;
}

.estrela.ativa {
  color: #f1c40f;
}

.form-actions {
  margin-top: 20px;
}

.btn-primary {
  padding: 10px 20px;
  background-color: #2c3e50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.btn-primary:disabled {
  background-color: #95a5a6;
}
</style> 